import { Platform } from 'ionic-angular';
import { JSBridge } from '../lib/jsbridge';

export class AppGlobal {
    private static jsbridge: JSBridge = new JSBridge();

    /**
      * 注册调用JS函数监听
      */
    static setupWebViewJavascriptBridge(callback) {
        this.jsbridge.setupWebViewJavascriptBridge(function (bridge) {
            //调用端统一使用此方法名，具体调用逻辑再data中使用'method'标识。
            bridge.registerHandler("callJsFunction", callback);
        })
    }

    /**
     * 调用原生方法，有返回回调
     */
    static callHandler(data: string, callback) {
        this.jsbridge.callHandler("callNativeFunction", data, callback);
    }

    /**
     * 调用原生方法,无返回回调
     */
    static send(data: string) {
        this.callHandler(data, function (responseData) { });
    }

    /**
      * 是否真机环境
      * @return {boolean}
      */
    static isMobile(platform: Platform): boolean {
        return platform.is('mobile') && !platform.is('mobileweb');
    }
    /**
     * 是否android真机环境
     * @return {boolean}
     */
    static isAndroid(platform: Platform): boolean {
        return this.isMobile(platform) && platform.is('android');
    }
    /**
     * 是否ios真机环境
     * @return {boolean}
     */
    static isIos(platform: Platform): boolean {
        return this.isMobile(platform) && (platform.is('ios') || platform.is('ipad') || platform.is('iphone'));
    }

}



