import { AppGlobal } from './AppGlobal';
import { Nav, Toast, ToastController, App, Platform } from 'ionic-angular';
import { Component, ViewChild, OnInit } from '@angular/core';

@Component({
    templateUrl: 'app.html'
})
export class MyApp implements OnInit {
    @ViewChild(Nav) nav: Nav;
    constructor(public platform: Platform, public toastCtrl: ToastController) {
        platform.ready().then(() => {
            let typeName = this.getQueryVariable("type");
            if (typeName === "home") {
                this.nav.setRoot('HomePage');
            } else if (typeName === "about") {
                this.nav.setRoot('AboutPage');
            } else if (typeName === "contact") {
                this.nav.setRoot('ContactPage');
            } else if (typeName === "echarts") {
                this.nav.setRoot('EchartsPage');
            } else if (typeName === "arcgis") {
                this.nav.setRoot('ArcgisPage');
            } else {
                this.nav.setRoot('TabsPage');   //不加参数默认为浏览器预览模式
            }

            //注册JSBridge
            AppGlobal.setupWebViewJavascriptBridge(function (data, responseCallback) {
                let toast = this.toastCtrl.create({
                    message: data,
                    duration: 2000,
                    position: 'bottom'
                });
                toast.present();

                responseCallback(data);//返回给调用端的数据，如果没有返回，可以不用此句
            });
        })
    }

    ngOnInit() {

    }

    getQueryVariable(variable: string): string {
        let query = window.location.search.substring(1);
        let vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split("=");
            if (pair[0] == variable) { return pair[1]; }
        }
        return "";
    }
}



